.header {
    position: relative;
    width: 100%;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: white;
}

.header .header-message {
    font-size: 2rem;
    color: #EA626B;
    font-weight: 800;
}

.header #header-description {
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 10px;
    color: #EA626B;
    font-weight: 600;
    margin-right: 10px;
}


